
import React from "react";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Avatar, AvatarFallback } from "@/components/ui/avatar";
import { 
  Brain, Volume2, Mic, Loader2, Settings, MessageSquare, Sliders, 
  Trash2, Radio, Smartphone, Zap, Shield, Phone, ArrowLeft
} from "lucide-react";

interface VapiAgentDetailViewProps {
  agent: any;
  details: any;
  isLoading: boolean;
  onUpdateField: (field: string, value: any) => void;
  onUpdateSystemMessage: (content: string) => void;
  onDelete: (agent: any) => void;
  onPublishToggle: (agent: any) => void;
  isPublished: boolean;
  isSaving: boolean;
  onTalkToAssistant: () => void;
  // Constants passed from parent
  MODEL_PROVIDERS: any[];
  VOICE_PROVIDERS: any[];
  TRANSCRIBER_PROVIDERS: any[];
  getProviderModels: (provider: string) => any[];
  getProviderVoices: (provider: string) => any[];
  getTranscriberModels: (provider: string) => any[];
  normalizeProviderValue: (value: string, type: string) => string;
  getInitials: (name: string) => string;
}

const VapiAgentDetailView: React.FC<VapiAgentDetailViewProps> = ({
  agent,
  details,
  isLoading,
  onUpdateField,
  onUpdateSystemMessage,
  onDelete,
  onPublishToggle,
  isPublished,
  isSaving,
  onTalkToAssistant,
  MODEL_PROVIDERS,
  VOICE_PROVIDERS,
  TRANSCRIBER_PROVIDERS,
  getProviderModels,
  getProviderVoices,
  getTranscriberModels,
  normalizeProviderValue,
  getInitials
}) => {
  if (isLoading) {
    return (
      <div className="flex flex-col items-center justify-center py-20 min-h-[400px]">
        <Loader2 className="h-10 w-10 animate-spin text-indigo-500 mb-4" />
        <p className="text-white/60">Fetching Vapi agent details...</p>
      </div>
    );
  }

  return (
    <div className="flex flex-col h-full bg-[#0b0f2e]">
      {/* Header */}
      <div className="p-6 border-b border-white/10 flex items-center justify-between sticky top-0 bg-[#0b0f2e] z-10">
        <div className="flex items-center space-x-4">
          <Avatar className="h-16 w-16 ring-2 ring-indigo-500/20">
            <AvatarFallback className="text-white bg-gradient-to-br from-indigo-500/30 to-purple-500/30 text-xl font-bold">
              {getInitials(agent.name)}
            </AvatarFallback>
          </Avatar>
          <div>
            <h2 className="text-2xl font-bold text-white mb-1">{agent.name}</h2>
            <div className="flex items-center space-x-4">
              <Badge variant="secondary" className="bg-green-500/20 text-green-400 border border-green-500/30">
                Active
              </Badge>
              <span className="text-sm text-white/50 font-mono">
                ID: {String(agent.id).substring(0, 12)}...
              </span>
              {isSaving && (
                <div className="flex items-center text-xs text-blue-400">
                  <Loader2 className="h-3 w-3 mr-1 animate-spin" />
                  Auto-saving...
                </div>
              )}
            </div>
          </div>
        </div>

        <div className="flex items-center space-x-3">
          <Button
            size="sm"
            onClick={onTalkToAssistant}
            className="bg-gradient-to-r from-teal-500 to-cyan-600 hover:from-teal-600 hover:to-cyan-700 text-white shadow-lg"
          >
            <Radio className="h-4 w-4 mr-2" />
            Talk
          </Button>

          <Button
            variant={isPublished ? "default" : "outline"}
            size="sm"
            onClick={() => onPublishToggle(agent)}
            className={isPublished
              ? "bg-green-600 hover:bg-green-700 text-white"
              : "text-white bg-transparent hover:bg-white/10 border-white/20"
            }
          >
            {isPublished ? "Published" : "Publish"}
          </Button>

          <Button
            variant="outline"
            size="sm"
            onClick={() => onDelete(agent)}
            className="text-red-400 hover:text-red-300 hover:bg-red-500/10 border-red-400/30"
          >
            <Trash2 className="h-4 w-4" />
          </Button>
        </div>
      </div>

      {/* Content */}
      <div className="flex-1 p-8 overflow-y-auto neon-fire-scroll">
        <Tabs defaultValue="model" className="w-full">
          <TabsList className="flex flex-wrap items-center gap-2 mb-8 bg-transparent p-0 h-auto">
            <TabsTrigger 
              value="model" 
              className="flag-tab h-12 min-w-[140px] border-0 bg-white/5 data-[state=active]:bg-gradient-to-r data-[state=active]:from-indigo-600 data-[state=active]:to-violet-600 data-[state=active]:text-white text-white/60 hover:text-white hover:bg-white/10 transition-all clip-path-flag mr-[-15px] z-10 data-[state=active]:z-20"
            >
              <Brain className="h-4 w-4 mr-2" />
              Model
            </TabsTrigger>
            <TabsTrigger 
              value="voice" 
              className="flag-tab h-12 min-w-[140px] border-0 bg-white/5 data-[state=active]:bg-gradient-to-r data-[state=active]:from-indigo-600 data-[state=active]:to-violet-600 data-[state=active]:text-white text-white/60 hover:text-white hover:bg-white/10 transition-all clip-path-flag mr-[-15px] z-10 data-[state=active]:z-20"
            >
              <Volume2 className="h-4 w-4 mr-2" />
              Voice
            </TabsTrigger>
            <TabsTrigger 
              value="transcriber" 
              className="flag-tab h-12 min-w-[140px] border-0 bg-white/5 data-[state=active]:bg-gradient-to-r data-[state=active]:from-indigo-600 data-[state=active]:to-violet-600 data-[state=active]:text-white text-white/60 hover:text-white hover:bg-white/10 transition-all clip-path-flag mr-[-15px] z-10 data-[state=active]:z-20"
            >
              <Mic className="h-4 w-4 mr-2" />
              Transcriber
            </TabsTrigger>
            <TabsTrigger 
              value="advanced" 
              className="flag-tab h-12 min-w-[140px] border-0 bg-white/5 data-[state=active]:bg-gradient-to-r data-[state=active]:from-indigo-600 data-[state=active]:to-violet-600 data-[state=active]:text-white text-white/60 hover:text-white hover:bg-white/10 transition-all clip-path-flag z-0"
            >
              <Settings className="h-4 w-4 mr-2" />
              Advanced
            </TabsTrigger>
          </TabsList>

          <TabsContent value="model" className="space-y-6">
            <Card className="neo-card border-none bg-white/5">
              <CardHeader>
                <CardTitle className="flex items-center text-white gap-2">
                  <Brain className="h-5 w-5 text-indigo-400" />
                  Model Configuration
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="text-sm font-medium text-white/70">Provider</label>
                    <Select
                      value={normalizeProviderValue(details?.model?.provider || '', 'model')}
                      onValueChange={(value) => onUpdateField('model.provider', value)}
                    >
                      <SelectTrigger className="mt-1 bg-white/5 border-white/10 text-white">
                        <SelectValue placeholder={details?.model?.provider || 'Select provider'} />
                      </SelectTrigger>
                      <SelectContent className="bg-[#0b0f2e] border-white/10 text-white">
                        {MODEL_PROVIDERS.map((provider) => (
                          <SelectItem key={provider.id} value={provider.id}>
                            <div className="flex items-center gap-2">
                              <span>{provider.name}</span>
                            </div>
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                  <div>
                    <label className="text-sm font-medium text-white/70">Model</label>
                    <Select
                      value={details?.model?.model || ''}
                      onValueChange={(value) => onUpdateField('model.model', value)}
                    >
                      <SelectTrigger className="mt-1 bg-white/5 border-white/10 text-white">
                        <SelectValue placeholder={details?.model?.model || 'Select model'} />
                      </SelectTrigger>
                      <SelectContent className="bg-[#0b0f2e] border-white/10 text-white">
                        {getProviderModels(details?.model?.provider || 'openai').map((model) => (
                          <SelectItem key={model.id} value={model.id}>
                            <div className="flex flex-col">
                              <span className="font-medium text-white">{model.name}</span>
                              <span className="text-xs text-white/50">{model.id}</span>
                            </div>
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                </div>
                <div className="grid grid-cols-3 gap-4">
                  <div>
                    <label className="text-sm font-medium text-white/70">Temperature</label>
                    <Input 
                      className="bg-white/5 border-white/10 text-white mt-1"
                      type="number" min="0" max="1" step="0.1"
                      value={details?.model?.temperature || 0.7}
                      onChange={(e) => onUpdateField('model.temperature', parseFloat(e.target.value))}
                    />
                  </div>
                  <div>
                    <label className="text-sm font-medium text-white/70">Max Tokens</label>
                    <Input 
                      className="bg-white/5 border-white/10 text-white mt-1"
                      type="number"
                      value={details?.model?.maxTokens || 1000}
                      onChange={(e) => onUpdateField('model.maxTokens', parseInt(e.target.value))}
                    />
                  </div>
                </div>
              </CardContent>
            </Card>

            <Card className="neo-card border-none bg-white/5">
              <CardHeader>
                <CardTitle className="flex items-center text-white gap-2">
                  <MessageSquare className="h-5 w-5 text-indigo-400" />
                  System Prompt
                </CardTitle>
              </CardHeader>
              <CardContent>
                <Textarea
                  className="bg-white/5 border-white/10 text-white min-h-[300px]"
                  value={details?.model?.messages?.[0]?.content || details?.systemMessage || ''}
                  onChange={(e) => onUpdateSystemMessage(e.target.value)}
                  placeholder="You are a helpful assistant..."
                />
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="voice" className="space-y-6">
            <Card className="neo-card border-none bg-white/5">
              <CardHeader>
                <CardTitle className="flex items-center text-white gap-2">
                  <Volume2 className="h-5 w-5 text-indigo-400" />
                  Voice Configuration
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-6">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="text-sm font-medium text-white/70">Voice Provider</label>
                    <Select
                      value={normalizeProviderValue(details?.voice?.provider || '', 'voice')}
                      onValueChange={(value) => onUpdateField('voice.provider', value)}
                    >
                      <SelectTrigger className="mt-1 bg-white/5 border-white/10 text-white">
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent className="bg-[#0b0f2e] border-white/10 text-white">
                        {VOICE_PROVIDERS.map((p) => (
                          <SelectItem key={p.id} value={p.id}>{p.name}</SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                  <div>
                    <label className="text-sm font-medium text-white/70">Voice ID</label>
                    <Select
                      value={details?.voice?.voiceId || ''}
                      onValueChange={(value) => onUpdateField('voice.voiceId', value)}
                    >
                      <SelectTrigger className="mt-1 bg-white/5 border-white/10 text-white">
                        <SelectValue placeholder="Select voice..." />
                      </SelectTrigger>
                      <SelectContent className="bg-[#0b0f2e] border-white/10 text-white">
                        {getProviderVoices(details?.voice?.provider || '11labs').map((v) => (
                          <SelectItem key={v.id} value={v.id}>{v.name}</SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="transcriber" className="space-y-6">
             <Card className="neo-card border-none bg-white/5">
               <CardHeader>
                 <CardTitle className="flex items-center text-white gap-2">
                   <Mic className="h-5 w-5 text-indigo-400" />
                   Transcriber
                 </CardTitle>
               </CardHeader>
               <CardContent className="space-y-4">
                 <div className="grid grid-cols-2 gap-4">
                    <div>
                      <label className="text-sm font-medium text-white/70">STT Provider</label>
                      <Select
                        value={details?.transcriber?.provider || 'deepgram'}
                        onValueChange={(value) => onUpdateField('transcriber.provider', value)}
                      >
                        <SelectTrigger className="mt-1 bg-white/5 border-white/10 text-white">
                          <SelectValue />
                        </SelectTrigger>
                        <SelectContent className="bg-[#0b0f2e] border-white/10 text-white">
                          {TRANSCRIBER_PROVIDERS.map((p) => (
                            <SelectItem key={p.id} value={p.id}>{p.name}</SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                    </div>
                    <div>
                      <label className="text-sm font-medium text-white/70">Language</label>
                      <Input 
                        className="bg-white/5 border-white/10 text-white mt-1"
                        value={details?.transcriber?.language || 'en'}
                        onChange={(e) => onUpdateField('transcriber.language', e.target.value)}
                      />
                    </div>
                 </div>
               </CardContent>
             </Card>
          </TabsContent>

          <TabsContent value="advanced" className="space-y-6">
             <Card className="neo-card border-none bg-white/5">
               <CardHeader>
                 <CardTitle>Call Settings</CardTitle>
               </CardHeader>
               <CardContent className="space-y-4">
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <label className="text-sm font-medium text-white/70">Max Duration (sec)</label>
                      <Input 
                        type="number"
                        className="bg-white/5 border-white/10 text-white mt-1"
                        value={details?.maxDurationSeconds || 600}
                        onChange={(e) => onUpdateField('maxDurationSeconds', parseInt(e.target.value))}
                      />
                    </div>
                    <div>
                      <label className="text-sm font-medium text-white/70">Background Sound</label>
                      <Select
                        value={details?.backgroundSound || 'off'}
                        onValueChange={(value) => onUpdateField('backgroundSound', value)}
                      >
                        <SelectTrigger className="mt-1 bg-white/5 border-white/10 text-white">
                          <SelectValue />
                        </SelectTrigger>
                        <SelectContent className="bg-[#0b0f2e] border-white/10 text-white">
                          <SelectItem value="off">Off</SelectItem>
                          <SelectItem value="office">Office</SelectItem>
                          <SelectItem value="cafe">Cafe</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                  </div>
               </CardContent>
             </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
};

export default VapiAgentDetailView;
