import React, { useState, useMemo, useEffect, useRef } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Tabs, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Search, Play, Pause, User, Mic } from 'lucide-react';
import { cn } from '@/lib/utils';

interface VoiceLibraryModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSelectVoice: (voiceId: string) => void;
  voices: any[];
}

export function VoiceLibraryModal({ isOpen, onClose, onSelectVoice, voices }: VoiceLibraryModalProps) {
  const [selectedProvider, setSelectedProvider] = useState<string>('all');
  const [searchQuery, setSearchQuery] = useState('');
  const [genderFilter, setGenderFilter] = useState('all');
  const [accentFilter, setAccentFilter] = useState('all');
  const [playingVoiceId, setPlayingVoiceId] = useState<string | null>(null);
  
  const audioRef = useRef<HTMLAudioElement | null>(null);

  // Stop audio when modal closes
  useEffect(() => {
    if (!isOpen) {
      stopAudio();
    }
  }, [isOpen]);

  const stopAudio = () => {
    if (audioRef.current) {
      audioRef.current.pause();
      audioRef.current.currentTime = 0;
    }
    setPlayingVoiceId(null);
  };

  const handlePlayPreview = (voiceId: string, previewUrl: string) => {
    if (playingVoiceId === voiceId) {
      // Toggle pause
      if (audioRef.current?.paused) {
        audioRef.current.play();
      } else {
        audioRef.current?.pause();
        setPlayingVoiceId(null);
      }
      return;
    }

    // Stop current
    stopAudio();

    if (!previewUrl) return;

    // Play new
    const audio = new Audio(previewUrl);
    audio.onended = () => setPlayingVoiceId(null);
    audio.onerror = () => {
        console.error("Error playing audio preview");
        setPlayingVoiceId(null);
    };
    
    audioRef.current = audio;
    audio.play().catch(e => console.error("Play error:", e));
    setPlayingVoiceId(voiceId);
  };

  const filteredVoices = useMemo(() => {
    return voices.filter(voice => {
      // Provider filter
      if (selectedProvider !== 'all') {
        // Normalize provider check - typically '11labs' vs 'ElevenLabs' in UI
        const provider = voice.provider?.toLowerCase() || '';
        if (selectedProvider === 'elevenlabs' && !provider.includes('eleven')) return false;
        if (selectedProvider === 'openai' && !provider.includes('openai')) return false;
        if (selectedProvider === 'cartesia' && !provider.includes('cartesia')) return false;
        if (selectedProvider === 'minimax' && !provider.includes('minimax')) return false;
      }

      // Search filter
      if (searchQuery) {
        const query = searchQuery.toLowerCase();
        const name = voice.voice_name?.toLowerCase() || '';
        const id = voice.voice_id?.toLowerCase() || '';
        if (!name.includes(query) && !id.includes(query)) return false;
      }

      // Gender filter
      if (genderFilter !== 'all') {
        const gender = voice.gender?.toLowerCase() || '';
        if (gender !== genderFilter) return false;
      }

      // Accent filter
      if (accentFilter !== 'all') {
        const accent = voice.accent?.toLowerCase() || '';
        if (!accent.includes(accentFilter)) return false;
      }

      return true;
    });
  }, [voices, selectedProvider, searchQuery, genderFilter, accentFilter]);

  // Extract unique accents for filter
  const uniqueAccents = useMemo(() => {
    const accents = new Set<string>();
    voices.forEach(v => {
      if (v.accent) accents.add(v.accent.toLowerCase());
    });
    return Array.from(accents);
  }, [voices]);

  return (
    <Dialog open={isOpen} onOpenChange={(open) => !open && onClose()}>
      <DialogContent className="sm:max-w-[1200px] rounded-lg h-[80vh] flex flex-col bg-[#0b0f2e] border-white/10 text-white p-0 gap-0">
        <DialogHeader className="p-6 pb-2 border-b border-white/5">
          <DialogTitle className="text-xl font-bold flex items-center gap-2">
            Select Voice
          </DialogTitle>
        </DialogHeader>

        <div className="p-6 space-y-6 flex-1 overflow-hidden flex flex-col">
          {/* Controls */}
          <div className="space-y-4">
            {/* Provider Tabs */}
            <Tabs value={selectedProvider} onValueChange={setSelectedProvider} className="w-full">
              <TabsList className="bg-white/5 h-auto p-1 flex-wrap justify-start gap-1 w-full sm:w-auto">
                <TabsTrigger value="all" className="data-[state=active]:bg-orange-500/20 data-[state=active]:text-orange-400 text-white/60 text-xs px-4 py-2">All</TabsTrigger>
                <TabsTrigger value="elevenlabs" className="data-[state=active]:bg-orange-500/20 data-[state=active]:text-orange-400 text-white/60 text-xs px-4 py-2">ElevenLabs</TabsTrigger>
                <TabsTrigger value="cartesia" className="data-[state=active]:bg-orange-500/20 data-[state=active]:text-orange-400 text-white/60 text-xs px-4 py-2">Cartesia</TabsTrigger>
                <TabsTrigger value="minimax" className="data-[state=active]:bg-orange-500/20 data-[state=active]:text-orange-400 text-white/60 text-xs px-4 py-2">MiniMax</TabsTrigger>
                <TabsTrigger value="openai" className="data-[state=active]:bg-orange-500/20 data-[state=active]:text-orange-400 text-white/60 text-xs px-4 py-2">OpenAI</TabsTrigger>
              </TabsList>
            </Tabs>

            {/* Filters */}
            <div className="flex flex-col sm:flex-row gap-3">
              <div className="relative flex-1">
                <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-white/40" />
                <Input 
                  placeholder="Search voices..." 
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="pl-9 bg-white/5 border-white/10 text-white focus:border-orange-500/50 placeholder:text-white/30"
                />
              </div>
              <Select value={genderFilter} onValueChange={setGenderFilter}>
                <SelectTrigger className="w-full sm:w-[150px] bg-white/5 border-white/10 text-white focus:border-orange-500/50">
                  <SelectValue placeholder="Gender" />
                </SelectTrigger>
                <SelectContent className="bg-[#0b0f2e] border-white/10 text-white">
                  <SelectItem value="all" className="focus:bg-white/10 focus:text-white">All Genders</SelectItem>
                  <SelectItem value="male" className="focus:bg-white/10 focus:text-white">Male</SelectItem>
                  <SelectItem value="female" className="focus:bg-white/10 focus:text-white">Female</SelectItem>
                </SelectContent>
              </Select>
              <Select value={accentFilter} onValueChange={setAccentFilter}>
                <SelectTrigger className="w-full sm:w-[180px] bg-white/5 border-white/10 text-white focus:border-orange-500/50">
                  <SelectValue placeholder="Accent" />
                </SelectTrigger>
                <SelectContent className="bg-[#0b0f2e] border-white/10 text-white">
                  <SelectItem value="all" className="focus:bg-white/10 focus:text-white">All Accents</SelectItem>
                  {uniqueAccents.map(accent => (
                     <SelectItem key={accent} value={accent} className="focus:bg-white/10 focus:text-white capitalize">{accent}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </div>

          {/* Voice List */}
          <div className="flex-1 overflow-y-auto neon-fire-scroll rounded-lg border border-white/5 bg-black/20">
            {filteredVoices.length === 0 ? (
              <div className="flex flex-col items-center justify-center h-full text-white/40 p-8">
                <Mic className="h-12 w-12 mb-4 opacity-20" />
                <p>No voices found matching your criteria</p>
              </div>
            ) : (
                <div className="grid grid-cols-1 divide-y divide-white/5">
                {filteredVoices.map((voice) => (
                    <div 
                        key={voice.voice_id} 
                        className="group flex flex-col sm:flex-row items-center gap-4 p-4 hover:bg-white/5 transition-colors"
                    >
                        {/* Play Button */}
                        <div className="flex-shrink-0">
                            <Button
                                size="icon"
                                variant="ghost"
                                className={cn(
                                    "h-10 w-10 rounded-full border border-white/10 hover:border-orange-500 hover:text-orange-500",
                                    playingVoiceId === voice.voice_id && "border-orange-500 text-orange-500 bg-orange-500/10"
                                )}
                                onClick={(e) => {
                                    e.stopPropagation();
                                    handlePlayPreview(voice.voice_id, voice.preview_audio_url);
                                }}
                            >
                                {playingVoiceId === voice.voice_id ? <Pause className="h-4 w-4" /> : <Play className="h-4 w-4 ml-1" />}
                            </Button>
                        </div>

                        {/* Avatar/Icon & Info */}
                        <div className="flex-1 w-full sm:w-auto flex items-center gap-4 cursor-pointer" onClick={() => onSelectVoice(voice.voice_id)}>
                            <div className="h-10 w-10 rounded-full bg-gradient-to-br from-orange-500/20 to-purple-500/20 flex items-center justify-center border border-white/10">
                                <User className="h-5 w-5 text-white/60" />
                            </div>
                            
                            <div className="flex-1 min-w-0">
                                <div className="flex items-center gap-2 mb-1">
                                    <h4 className="font-medium text-white truncate">{voice.voice_name || 'Unnamed Voice'}</h4>
                                    <span className="text-xs px-2 py-0.5 rounded-full bg-white/5 text-white/40 border border-white/5">
                                        {voice.provider || 'Retell'}
                                    </span>
                                </div>
                                <div className="flex flex-wrap items-center gap-2 text-xs text-white/40">
                                    {voice.gender && <span className="capitalize">{voice.gender}</span>}
                                    {voice.gender && voice.accent && <span>•</span>}
                                    {voice.accent && <span className="capitalize">{voice.accent}</span>}
                                    <span className="hidden sm:inline-block ml-2 opacity-50 font-mono">{voice.voice_id}</span>
                                </div>
                            </div>
                        </div>

                         {/* Actions */}
                         <div className="flex-shrink-0 w-full sm:w-auto flex justify-end">
                            <Button 
                                variant="outline" 
                                className="bg-transparent border-white/10 hover:bg-orange-500 hover:text-white hover:border-orange-500 text-xs h-8"
                                onClick={() => onSelectVoice(voice.voice_id)}
                            >
                                Select
                            </Button>
                         </div>
                    </div>
                ))}
                </div>
            )}
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
}
