import React, { useState } from 'react';
import { useQuery } from '@tanstack/react-query';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { 
  Bot, 
  Star, 
  Search, 
  Filter,
  ArrowRight,
  Sparkles,
  Plus,
  ArrowLeft,
  Mic, 
  Smartphone,
  Phone
} from 'lucide-react';
import {
  getAllDefaultAssistantTemplates,
  getTemplateCategories,
  useTemplate,
  type DefaultAssistantTemplate,
  type TemplateCategory
} from '@/api/services/defaultAssistantTemplates/defaultAssistantTemplateService';

interface TemplateSelectionDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  onTemplateSelect: (templateData: any) => void;
  onCreateCustom: (type?: 'vapi' | 'retell') => void;
}

export const TemplateSelectionDialog: React.FC<TemplateSelectionDialogProps> = ({
  open,
  onOpenChange,
  onTemplateSelect,
  onCreateCustom
}) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState<string>('all');
  const [step, setStep] = useState<'initial' | 'service_selection'>('initial');

  // Fetch templates
  const { data: templatesData, isLoading: templatesLoading } = useQuery({
    queryKey: ['defaultAssistantTemplates', { category: selectedCategory !== 'all' ? selectedCategory : undefined }],
    queryFn: () => {
      const filters: any = { is_active: true };
      if (selectedCategory !== 'all') {
        filters.category = selectedCategory;
      }
      return getAllDefaultAssistantTemplates(filters);
    },
    enabled: open
  });

  // Fetch categories
  const { data: categoriesData } = useQuery({
    queryKey: ['templateCategories'],
    queryFn: getTemplateCategories,
    enabled: open
  });

  const templates = templatesData?.data || [];
  const categories = categoriesData?.data || [];

  // Filter templates based on search term
  const filteredTemplates = templates.filter((template: DefaultAssistantTemplate) =>
    template.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    template.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
    template.category.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const handleTemplateSelect = async (template: DefaultAssistantTemplate) => {
    try {
      const templateData = await useTemplate(template.template_id);
      onTemplateSelect(templateData.data);
      onOpenChange(false);
    } catch (error) {
      console.error('Error using template:', error);
    }
  };

  const resetState = () => {
    setStep('initial');
    setSearchTerm('');
    setSelectedCategory('all');
  };

  const handleOpenChange = (isOpen: boolean) => {
    if (!isOpen) {
      resetState();
    }
    onOpenChange(isOpen);
  };

  const getCategoryColor = (category: string) => {
    const colors: Record<string, string> = {
      'Business': 'bg-blue-100 text-blue-800',
      'Sales': 'bg-green-100 text-green-800',
      'Support': 'bg-purple-100 text-purple-800',
      'Scheduling': 'bg-orange-100 text-orange-800',
      'General': 'bg-gray-100 text-gray-800'
    };
    return colors[category] || 'bg-gray-100 text-gray-800';
  };

  return (
    <Dialog open={open} onOpenChange={handleOpenChange}>
      <DialogContent className="
    max-w-7xl min-h-[700px] overflow-hidden
    bg-[#020617]
    border border-cyan-400/30
    shadow-[0_0_30px_rgba(34,211,238,0.35)]
    text-slate-200
  ">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2 text-white">
            <Bot className="h-5 w-5 text-white" />
            {step === 'initial' ? 'Choose How to Create Your Assistant' : 'Select AI Service Provider'}
          </DialogTitle>
        </DialogHeader>

        {step === 'initial' ? (
        <div className="space-y-6">
          {/* Choice Cards */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {/* Use Template Card */}
            <Card className="  cursor-pointer
  bg-cyan-500/10
  border border-cyan-400/30
  hover:shadow-[0_0_30px_rgba(34,211,238,0.45)]
  transition">
              <CardHeader className="pb-3">
                <CardTitle className="flex items-center gap-2 text-white">
                  <Sparkles className="h-5 w-5 text-white" />
                  Use Default Template
                </CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-white text-sm mb-4">
                  Start with a pre-configured template designed for specific use cases. 
                  Perfect for quick setup with proven configurations.
                </p>
                <div className="flex items-center gap-2 text-xs text-white">
                  <Star className="h-3 w-3 text-white" />
                  Recommended for beginners
                </div>
              </CardContent>
            </Card>

            {/* Create Custom Card */}
            <Card 
              className="cursor-pointer bg-cyan-500/10
  border border-cyan-400/30
  hover:shadow-[0_0_30px_rgba(34,211,238,0.45)] transition"
              onClick={() => setStep('service_selection')}
            >
              <CardHeader className="pb-3">
                <CardTitle className="flex items-center gap-2 text-white">
                  <Plus className="h-5 w-5 text-white" />
                  Create Custom Assistant
                </CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-white text-sm mb-4">
                  Build your assistant from scratch with full control over all settings. 
                  Ideal for specific requirements and advanced users.
                </p>
                <div className="flex items-center gap-2 text-xs text-white">
                  <Bot className="h-3 w-3 text-white" />
                  Full customization available
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Template Selection Section */}
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <h3 className="text-lg font-medium">Available Templates</h3>
              <div className="flex gap-2">
                <div className="relative">
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-white h-4 w-4  z-10" />
                  <Input
                    placeholder="Search templates..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className=" pl-10 w-64
    bg-black/40
    border border-cyan-400/30
    text-slate-200
    placeholder:text-slate-500
    focus:ring-cyan-400/50"
                  />
                </div>
                <Select value={selectedCategory} onValueChange={setSelectedCategory}>
                  <SelectTrigger className="
    w-48
    bg-black/40
    border border-cyan-400/30
    text-slate-200
  ">
                    <Filter className="h-4 w-4 mr-2" />
                    <SelectValue placeholder="Category" />
                  </SelectTrigger>
                  <SelectContent className="bg-[#020617] text-white border border-cyan-400/30">

                    <SelectItem value="all">All Categories</SelectItem>
                    {categories.map((category: TemplateCategory) => (
                      <SelectItem key={category.category} value={category.category}>
                        {category.category} ({category.count})
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </div>

            {/* Templates Grid */}
            <div className="max-h-96 overflow-y-auto">
              {templatesLoading ? (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                  {[...Array(6)].map((_, i) => (
                    <Card key={i}
  className="
    cursor-pointer
    bg-white/5
    border border-cyan-400/20
    hover:shadow-[0_0_25px_rgba(34,211,238,0.4)]
    transition
  "
>

                      <CardHeader>
                        <div className="h-4 bg-gray-200 rounded w-3/4"></div>
                        <div className="h-3 bg-gray-200 rounded w-1/2"></div>
                      </CardHeader>
                      <CardContent>
                        <div className="space-y-2">
                          <div className="h-3 bg-gray-200 rounded"></div>
                          <div className="h-3 bg-gray-200 rounded w-5/6"></div>
                        </div>
                      </CardContent>
                    </Card>
                  ))}
                </div>
              ) : filteredTemplates.length === 0 ? (
               <Card
  className="
    cursor-pointer
    bg-white/5
    border border-cyan-400/20
    hover:shadow-[0_0_25px_rgba(34,211,238,0.4)]
    transition
  "
>

                  <CardContent className="p-8 text-center">
                    <Bot className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                    <h3 className="text-lg font-medium text-gray-900 mb-2">No templates found</h3>
                    <p className="text-gray-600 mb-4">
                      {searchTerm || selectedCategory !== 'all' 
                        ? 'Try adjusting your search or filter criteria.'
                        : 'No templates are currently available.'
                      }
                    </p>
                    <Button onClick={() => setStep('service_selection')}>
                      Create Custom Assistant Instead
                    </Button>
                  </CardContent>
                </Card>
              ) : (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                  {filteredTemplates.map((template: DefaultAssistantTemplate) => (
                    <Card 
                      key={template.template_id} 
                       className="
    cursor-pointer
    bg-white/5
    border border-cyan-400/20
    hover:shadow-[0_0_25px_rgba(34,211,238,0.4)]
    transition
  "
                      onClick={() => handleTemplateSelect(template)}
                    >
                      <CardHeader className="pb-3">
                        <div className="flex items-start justify-between">
                          <div className="flex-1">
                            <CardTitle className="text-white flex items-center gap-2">
                              {/* {template.is_featured && <Star className="h-4 w-4 text-yellow-500 fill-current" />} */}
                              {template.name}
                            </CardTitle>
                            <div className="flex items-center gap-2 mt-2">
                              <Badge className={getCategoryColor(template.category)}>
                                {template.category}
                              </Badge>
                            </div>
                          </div>
                          <ArrowRight className="h-4 w-4 text-gray-400" />
                        </div>
                      </CardHeader>
                      <CardContent className="pt-0">
                        <p className="text-white/60 text-sm mb-3 line-clamp-2">
                          {template.description}
                        </p>
                        <div className="flex items-center justify-between text-xs text-gray-500">
                          {/* <div className="flex items-center gap-1">
                            <Users className="h-3 w-3" />
                            {template.usage_count || 0} uses
                          </div> */}
                          <Button size="sm" variant="ghost" className="h-6 px-2 text-xs bg-primary text-white w-full ">
                            Use Template
                          </Button>
                        </div>
                      </CardContent>
                    </Card>
                  ))}
                </div>
              )}
            </div>
          </div>
        </div>
        ) : (
          <div className="space-y-6">
            <Button 
              variant="ghost" 
              onClick={() => setStep('initial')}
              className="text-white/60 hover:text-white"
            >
              <ArrowLeft className="h-4 w-4 mr-2" />
              Back
            </Button>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6 p-4">
              {/* Vapi Service Card */}
              <Card 
                className="cursor-pointer bg-blue-500/10 border border-blue-400/30 hover:shadow-[0_0_30px_rgba(59,130,246,0.45)] transition group"
                onClick={() => {
                  onCreateCustom('vapi');
                  onOpenChange(false);
                }}
              >
                <CardHeader>
                  <CardTitle className="flex items-center gap-3 text-white text-2xl">
                    <div className="p-3 rounded-lg bg-blue-500/20 group-hover:bg-blue-500/30 transition-colors">
                      <Mic className="h-8 w-8 text-blue-400" />
                    </div>
                    Vapi Service
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <p className="text-white/80">
                    Advanced voice AI platform with ultra-low latency. 
                    Best for complex conversational flows and extensive customization.
                  </p>
                  <div className="flex flex-wrap gap-2">
                    <Badge variant="outline" className="border-blue-400/30 text-blue-300">Customizable</Badge>
                    <Badge variant="outline" className="border-blue-400/30 text-blue-300">Real-time</Badge>
                  </div>
                </CardContent>
              </Card>

              {/* Retell Service Card */}
              <Card 
                className="cursor-pointer bg-purple-500/10 border border-purple-400/30 hover:shadow-[0_0_30px_rgba(168,85,247,0.45)] transition group"
                onClick={() => {
                  onCreateCustom('retell');
                  onOpenChange(false);
                }}
              >
                <CardHeader>
                  <CardTitle className="flex items-center gap-3 text-white text-2xl">
                    <div className="p-3 rounded-lg bg-purple-500/20 group-hover:bg-purple-500/30 transition-colors">
                      <Phone className="h-8 w-8 text-purple-400" />
                    </div>
                    Retell AI
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <p className="text-white/80">
                    Powerful telephony and voice agent integration.
                    Ideal for phone-based assistants and seamless call handling.
                  </p>
                  <div className="flex flex-wrap gap-2">
                    <Badge variant="outline" className="border-purple-400/30 text-purple-300">Telephony</Badge>
                    <Badge variant="outline" className="border-purple-400/30 text-purple-300">Resilient</Badge>
                  </div>
                </CardContent>
              </Card>
            </div>
            
            <div className="text-center text-sm text-white/50 pt-4">
              Select the service provider that best fits your use case
            </div>
          </div>
        )}
      </DialogContent>
    </Dialog>
  );
};
